<template>
<div>
    <h1>用户</h1>
    <table border="1">
        <tr>
            <th>编号</th>
            <th>姓名</th>
            <th>年龄</th>
            <th>生日</th>
            <th>操作</th>
        </tr>
        <tr v-for="user in users" :key="user.id">
            <td>{{user.id}}</td>
            <td>{{user.name}}</td>
            <td>{{user.age}}</td>
            <td>{{user.bir}}</td>
            <td><a :href="'#/user/edit?id=' +user.id">修改</a><a href="javascript:;" @click="delRow(user.id)">删除</a></td>
        </tr>
    </table>

    <a href="#/user/add">添加</a>
    <router-view></router-view>
</div>
</template>

<script>
export default {
    name: "User",
    data() {
        return {
            users: []
        }
    },
    methods: {
        findAll() {
            this.$http.get("http://localhost:8989/vue/user/findAll").then((res) => {
                //console.log(res.data);
                this.users = res.data.results;
            }).catch((err) => {
                console.log(err);
            });
        },
        delRow(id) {
            //console.log(id);
            this.$http.get('http://localhost:8989/vue/user/delete?id=' + id).then(res => {
                if (res.data.success) {
                    //alert(res.data.msg);
                    this.findAll();
                }
            });
        },
    },
    components: {

    },
    created() {
        this.findAll();
    },
    //用来监听路由变化
    watch: {
        $route: {
            handler: function (val, oldVal) {
                //console.log(val);
                if (val.path == '/user') {
                    this.findAll();
                }
            },
            //深度观察监听
            deep: true
        }
    },
}
</script>

<style scoped>

</style>



