<template>
<div>
    <h1>用户添加</h1>
    <form>
        用户名：<input v-model="user.name" /><br />
        年龄：<input v-model="user.age" /><br />
        生日：<input v-model="user.bir" /><br />
        <button type="button" @click="saveUser">添加</button>
    </form>
</div>
</template>

<script>
export default {
    name: "UserAdd",
    data() {
        return {
            user: {}
        }
    },
    methods: {
        saveUser() {
            //console.log(this.user);
            this.$http.post('http://localhost:8989/vue/user/add', this.user)
                .then((res) => {
                    //console.log(res.data);
                    if (res.data.success) {
                        //切换路由
                        this.$router.push("/user");
                    }
                }).catch((err) => {

                });
        }
    },
    components: {

    },
    created() {

    },

}
</script>

<style scoped>

</style>



