<template>
<div>
    <h2>编辑用户信息</h2>
    <form action="">
        用户名：<input v-model="user.name" /><br />
        年龄：<input v-model="user.age" /><br />
        生日：<input v-model="user.bir" /><br />
        <input type="button" value="更新用户" @click="editUserInfo" />
    </form>
    <router-view></router-view>
</div>
</template>

<script>
export default {
    name: "UserEdit",
    data() {
        return {
            user: {
                id: "",
            }
        };
    },
    methods: {
        findOne() {
            this.$http.get('http://localhost:8989/vue/user/getUserById?id=' + this.user.id).then((res) => {
                //console.log(res.data);
                this.user = res.data;
            });
        },
        editUserInfo() {
            this.$http.post('http://localhost:8989/vue/user/update', this.user).then((res) => {
                if (res.data.success) {
                    this.$router.push('/user');
                }
            });
        }
    },
    created() {
        //console.log(this.$route.query.id);
        this.user.id = this.$route.query.id;
        this.findOne();
    }
}
</script>

<style scoped>

</style>



